  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.landing-nft .transaction .inner-div-card:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.landing-nft .transaction .inner-div-card h1{
