}
.landing-nft .roadmap .inner-div-main:before{
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: 33%;
  left: -10px;
  background-color: #fff; /* IE 9 */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
