:root{
    --background-main: #333645;
    --text-color-yellow: #ffaa06;
    --text-color-grey: #a8a7b0;
  }

.landing-nft .main-header{position: absolute;left: 0;top: 0px;width: 100%;z-index: 111;padding: 0 6%;background-color: transparent;}
.navbar{padding: .4rem 1rem!important;}
.navbar .button-head button.button-one{outline: none;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  background: linear-gradient(89.81deg, #1D98FF 0.12%, #2DC6FE 99.8%);color: #ffffff;
  border: none;transition: all 0.3s linear;border-radius: 50px;padding: 10px 40px;margin-left: 10px;
}
.navbar .button-head button.button-two{outline: none;background-color: #ffffff;border: none;transition: all 0.3s linear;border-radius: 6px;font-family: 'nunito';font-weight: 700;font-size: 16px;color: #ffffff;padding: 11px 26px;margin-left: 24px;}
.navbar .nav-links ul li .bell-image img{margin-bottom: 16px;cursor: pointer; }
.navbar .nav-links ul li {text-decoration: none;}
.navbar .nav-links ul li .dropdown button{background: transparent;border: none;outline: none;}
.navbar .nav-links ul li .dropdown button i{padding-left: 10px;font-size: 18px;}
.navbar .nav-links ul .launch-app{display: none;}
.navbar .nav-links ul li .dropdown button{padding-right: 14px;padding-left: 10px;}
.navbar .nav-link{ position: relative; text-decoration: none; color: #ffffff;
  font-family: 'poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 0px;}
.navbar .nav-link:hover{color: #2DC6FE;}
.navbar .navbar-toggler {border: none!important;    padding: .25rem .55rem;font-size: 14px;outline: none;}
.navbar .navbar-nav{text-align: center;margin-top: 10px;}
.navbar .navbar-collapse{text-align: center;}
.navbar .button-head span{background-color: #ffaa06;padding: 10px 25px; border-radius: 50px;}
.fa-bars {color: #2C3038;}

/* .hamburger {
  display: none;
} */

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
  background-color: #ffffff;
}
.navbar .navbar-toggler.collapsed .bar:nth-child(2) {
  opacity: unset;
}

.navbar .navbar-toggler.collapsed .bar:nth-child(1) {
  -webkit-transform: unset;
          transform: unset;
}

.navbar .navbar-toggler.collapsed .bar:nth-child(3) {
  -webkit-transform: unset;
          transform: unset;
}
.navbar .navbar-toggler .bar:nth-child(2) {
  opacity: 0;
}

.navbar .navbar-toggler .bar:nth-child(1) {
  -webkit-transform: translateY(8px) rotate(45deg);
          transform: translateY(8px) rotate(45deg);
}

.navbar .navbar-toggler .bar:nth-child(3) {
  -webkit-transform: translateY(-8px) rotate(-45deg);
          transform: translateY(-8px) rotate(-45deg);
}
.navbar-expand-lg .navbar-nav li{
  margin-right: 20px;
}
