*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.app-text {
    text-align: center;
    color: #61dafb;
  }

.landing-nft {
  background-color:#200F70;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  }

.auto-container{
  position:static;
  max-width:1640px;
  padding:0px 15px;
  margin:0 auto;
}
  /* Css For All Modal Same Background And Images Logo Start */
.input-feild-button .text-feild-modal .MuiFormControl-root .MuiInputBase-root .MuiOutlinedInput-notchedOutline{
  border: 1px solid #ffffff;
}
.modal-open .form-modal-landing-silver .input-feild-button .button-form-modal{
  background-color: #ffaa06;
  border: none;
  border-radius: 36px;
  color: #fff;
  padding: 10px 55px;
  margin-top: 8px;
}
.modal-open .form-modal-landing-silver .input-feild-button .text-feild-modal .MuiFormControl-root{
  width: 100%;
  
}
.modal-footer{
  display: none!important;
}
.modal-open .modal-header{
  border: none;
  padding: 2rem 1rem!important;
}
.modal-open .form-modal-landing-silver .input-feild-button .text-feild-modal .MuiFormControl-root .MuiInputBase-root{
  color: #ffffff;
  border-radius: 10px;
  padding-bottom: 5px;
}
  
.modal-open .form-modal-landing-silver .input-feild-button .text-feild-modal .MuiFormControl-root label{
  color: #ffffff;
  font-size: 12px;
  padding-top: 5px;
  padding-left: 10px;
  font-family: 'lato';
  font-weight: 400;
  
}
.modal-open .modal-content{
  border-radius: 15px;
  position: relative;
}
.modal-header {
  display: unset!important;
}
.modal-header .close{
  padding: 0.5rem 0.7rem!important;
  margin: -1rem 0rem -1rem auto!important;
}
.modal-header button.close{
  opacity: 1;
  color: #00D1FF;
  background-color: #2B3546;
  position: absolute;
  right: 30px;
  top: 44px;
  border-radius: 50%;
  box-shadow: none;
  outline: none;
  text-shadow: none;
  line-height: 29px;
  font-weight: 400;
  z-index: 1111;
  font-size: 26px;
}
.modal-header button span{
  font-size: 46px;
}
