.modal-open .modal-content .modal-body .white-listed .paragraph2{
  float: right;
}
.modal-open .modal-content .modal-body .white-listed .margin-cont{
  margin-left: 132px;
  padding-top: 20px;
}
.modal-header button{
outline: none;
box-shadow: none;
}
.modal-open .modal-dialog{
  margin: 230px auto;
  -webkit-box-shadow: 0px 4px 33px 3px rgba(0,0,0,0.60);
  -moz-box-shadow: 0px 4px 33px 3px rgba(0,0,0,0.60);
  shadow: 0px 4px 33px 3px rgba(0,0,0,0.60);
}
.modal-open .modal-body{
  padding-bottom: 40px;
}
  
.modal-open .form-modal-landing-silver .input-feild-button .text-mid-pr {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}
.modal-open .form-modal-landing-silver .input-feild-button{
  padding-top: 68px;
}
  
.modal-open .form-modal-landing-silver .input-feild-button .text-mid-pr p span {
  font-weight: 600;
  font-size: 15px;
}

.modal-open .register-modal {
  position: relative;
  border-radius: 15px;
}
.modal-open .register-modal .icon-area-modal-pur{
  position: absolute;
    top: -2px;
    left: 163px;
}
.modal-open .register-modal .icon-area-modal-pur img{
  width: 54%;
  padding-top: 40px;
  text-align: center;
}
  
.modal-open .register-modal .icon-area-modal-pur{
  text-align: center;
}
.modal-open .modal-dialog{
  margin: 150px auto;
  max-width: 660px;
}
.modal-open .modal-content{
background-color:#0F0E13;
  background-size: cover;
  width: 100%;
  border: 1px solid #00D1FF33;
  background-position: top center;
}
  
 
  
@media(max-width: 1920px){
  .auto-container{
    max-width: 1640px;
  }
}
@media(max-width: 1680px){
  .auto-container{
    max-width: 1540px;
  }
}

@media(max-width: 1500px){
  .auto-container{
    max-width: 1340px;
  }
  .modal-open .modal-dialog {
    margin: 150px auto;
    max-width: 594px;
}
}

@media(max-width: 1400px){
  .auto-container{
    max-width: 1300px;
  }
  .modal-open .modal-dialog {
    margin: 150px auto;
    max-width: 594px;
}
}


@media(max-width: 600px){
  .modal-header .close {
    padding: 0.3rem 0.5rem!important;
    margin: -1rem 0rem -1rem auto!important;
}
.modal-open .modal-dialog{
  max-width: 390px!important;
}
.modal-header .close {
  padding: 0.3rem 0.5rem!important;
}
.modal-open .modal-body {
  padding-bottom: 18px;
  padding-top: 22px;
}
}

  
@media(max-width: 400px){
  .modal-header .close {
    padding: 0.3rem 0.5rem!important;
    margin: -1rem 0rem -1rem auto!important;
}
.modal-open .modal-dialog {
  margin: 127px auto!important;
  max-width: 344px!important;
}
}

@media(max-width: 320px){
  .modal-open .modal-dialog {
    margin: 112px auto!important;
    max-width: 297px!important;
}
.modal-body{
  padding: 1rem 0px;
}
}
